import { toyService } from '../services/toy.service.js'

export const toyStore = {
  state: {
    toys: [],
    filterBy: {
      txt: '',
      type: null,
      inStock: false
    },
    sortBy: null
  },
  getters: {
    toys(state) {
      return state.toys
    },
    filterBy(state) {
      return state.filterBy
    }
  },
  mutations: {
    setToys(state, { toys }) {
      state.toys = toys
    },
    addToy(state, { toy }) {
      state.toys.unshift(toy)
    },
    updateToy(state, { toy }) {
      const idx = state.toys.findIndex(currToy => currToy._id === toy._id)
      state.toys.splice(idx, 1, toy)
    },
    removeToy(state, { toyId }) {
      const idx = state.toys.findIndex(currToy => currToy._id === toyId)
      state.toys.splice(idx, 1)
    },
    filterToys(state, { filterBy }) {
      state.filterBy = JSON.parse(JSON.stringify(filterBy))
    },
    sortToys(state, { sortBy }) {
      state.sortBy = sortBy
    }
  },
  actions: {
    async loadToys(context) {
      const criteria = {
        filterBy: context.state.filterBy,
        sortBy: context.state.sortBy
      }
      const toys = await toyService.query(criteria)
      context.commit({ type: 'setToys', toys })
      return toys
    },
    async loadToy(context, { toyId }) {
      const toy = await toyService.getById(toyId)
          context.commit({ type: 'updateToy', toy })
          return toy
    },
    async saveToy(context, { toy }) {
      const isEdit = !!toy._id
      const savedToy = await toyService.save(toy)
      context.commit({
        type: (isEdit) ? 'updateToy' : 'addToy',
        toy: savedToy
      })
      return savedToy
    },
    async removeToy(context, payload) {
      await toyService.remove(payload.toyId)
          context.commit(payload);
    }
  }
}