import Vue from 'vue'
import VueRouter from 'vue-router'
import aboutPage from '../views/about-page.vue'
import toyApp from '../views/toy-views/toy-app.vue'
import toyDetails from '../views/toy-views/toy-details.vue'
import toyEdit from '../views/toy-views/toy-edit.vue'
import loginPage from '../views/login-page.vue'
import singupPage from '@/views/signup-page.vue'
import userProfile from '../views/user-profile.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'ToyApp',
    component: toyApp
  },
  {
    path: '/about',
    name: 'About',
    component: aboutPage
  },
  {
    path: '/login',
    name: 'Login',
    component: loginPage
  },
  {
    path: '/singup',
    name: 'Singup',
    component: singupPage
  },
  {
    path: '/toy/details/:toyId',
    name: 'ToyDetails',
    component: toyDetails
  },
  {
    path: '/toy/edit/:toyId?',
    name: 'ToyEdit',
    component: toyEdit
  },
  {
    path: '/user/profile',
    name: 'UserProfile',
    component: userProfile
  }
]

const router = new VueRouter({
  routes
})

export default router
