<template>
  <section class="toy-preview">
    <router-link :to="'/toy/details/' + toy._id">
      <img :src="imgUrl" alt="product" />
    </router-link>
    <router-link :to="'/toy/details/' + toy._id">
      <div class="name">{{toy.name | capitalize}}</div>
    </router-link>
    <div class="price">{{toy.price | price}}</div>
  </section>
</template>

<script>
export default {
  computed: {
    imgUrl() {
    const defaultUrl = "http://res.cloudinary.com/ramus/image/upload/v1584397996/iwtdenwldxi67errhflg.png"
      return this.toy.imgUrl
        ? this.toy.imgUrl
        : defaultUrl;
    }
  },
  props: {
    toy: Object
  }
};
</script>
