<template>
  <div id="app" class="">
    <main-header></main-header>

    <router-view />

    <main-footer></main-footer>

    <chat-popup v-if="isChatOpen">
      <chat-app></chat-app>
    </chat-popup>
    <button v-else class="chat-btn" @click="toggleChat">Chat</button>
  </div>
</template>

<script>
import chatPopup from "./cmps/chat-popup.vue";
import chatApp from "@/cmps/chat-app.vue";
import mainHeader from '@/cmps/main-header.vue';
import mainFooter from '@/cmps/main-footer.vue';

export default {
  methods: {
    toggleChat() {
      this.$store.commit({ type: "toggleChat" });
    }
  },
  computed: {
    isChatOpen() {
      return this.$store.getters.isChat;
    }
  },
  components: {
    chatPopup,
    chatApp,
    mainHeader,
    mainFooter
  }
};
</script>