<template>
  <section class="main-header site-block">
    <header>
      <router-link to="/">
        <img src="@/assets/logo.png" alt="logo" />
      </router-link>

      <form @submit.prevent="filterToys(filterBy)">
        <input type="text" v-model="filterBy.txt" />
        <button>Search</button>
      </form>

      <div class="username">
        <span>Hello</span>
        <router-link class="user-link" to="/user/profile">{{userName}}</router-link>
      </div>
    </header>

    <nav-filter @input=" filterToys(filterBy)" v-model="filterBy.type"></nav-filter>
  </section>
</template>

<script>
import navFilter from "@/cmps/nav-filter";

export default {
  data() {
    return {
      filterBy: null
    };
  },
  computed: {
    userName() {
      let loggeninUser = this.$store.getters.loggedinUser;
      return loggeninUser ? loggeninUser.username : "Guest";
    }
  },
  methods: {
    getFilter() {
      this.filterBy = JSON.parse(JSON.stringify(this.$store.getters.filterBy));
    },
    filterToys(filterBy) {
      this.$store.commit({ type: "filterToys", filterBy });
      this.$store.dispatch({ type: "loadToys" });
    }
  },
  watch: {
    "this.$store.getters.filterBy.type"(oldVal, newVal) {
      this.filterBy.type = newVal;
    },
    "this.$store.getters.filterBy.inStock"(oldVal, newVal) {
      this.filterBy.inStock = newVal;
    }
  },
  created() {
    this.getFilter();
  },
  components: {
    navFilter
  }
};
</script>