<template>
  <section class="login-page">
    <form @submit.prevent="login(credentials)">
      <input
        name="email"
        type="text"
        v-model="credentials.email"
        class="login-box"
        placeholder="Email address"
      />
      <input
        name="password"
        type="password"
        v-model="credentials.password"
        class="login-box"
        placeholder="Password"
      />
      <button class="login-btn classic-btn">Login</button>
    </form>
    <router-link to="/singup">Dont have a user yet? Singup</router-link>
  </section>
</template>

<script>
import { userService } from "@/services/user.service.js";

export default {
  data() {
    return {
      credentials: {
        email: "",
        password: ""
      }
    };
  },
  methods: {
    async login(credentials) {
      try {
        await this.$store.dispatch({ type: "login", credentials });
        this.$router.push("/");
      } catch {
        console.log('Try again');
      }
    }
  }
};
</script>
