<template>
      <section class="color-picker">
        <div class="color-btn palette-white" @click="setColor('fdfdfd')"></div>
        <div class="color-btn palette-red" @click="setColor('ff8888')"></div>
        <div class="color-btn palette-orange" @click="setColor('ffcc88')"></div>
        <div class="color-btn palette-yellow" @click="setColor('ffff88')"></div>
        <div class="color-btn palette-green" @click="setColor('ccff99')"></div>
        <div class="color-btn palette-blue" @click="setColor('aaffee')"></div>
        <div class="color-btn palette-purple" @click="setColor('ddbbff')"></div>
      </section>
</template>

<script>
export default {
    data() {
        return {
            color: this.value
        }
    },
    methods: {
        setColor(colorHex) {
            this.color = colorHex
            this.$emit('input', this.color)
        },
        emitColor(ev) {
            
        }
    },
    props: {
        value: String
    }
}
</script>
