<template>
  <section class="chat-app">
    <ul class="msgs" ref="chatMsgs">
      <li v-for="msg in msgs" class="msg" :key="msg._id">{{msg.txt}}</li>
    </ul>
    <input type="text" v-model="txt" @keyup.enter="sendMsg(txt)" />
  </section>
</template>

<script>
export default {
  data() {
    return {
      txt: ""
    };
  },
  computed: {
    msgs() {
      return this.$store.getters.msgs;
    }
  },
  methods: {
    sendMsg(txt) {
      this.$store.commit({ type: "sendMsg", txt })
      this.txt = "";
      this.$store.dispatch({ type: "reply" })
        .then(() => {
            this.$refs.chatMsgs.scrollIntoView(false)
        })
    }
  },
  created() {
      document.body.addEventListener('keyup', (ev) => {
          if (ev.key === 'Escape')  this.$store.commit({ type: "closeChat" })
          
      })
  },
  destroyed() {
      document.body.removeEventListener('keyup', (ev) => {
          if (ev.key === 'Escape')  this.$store.commit({ type: "closeChat" })
          
      })
  }
}
</script>