<template>
  <div class="toy-app main-layout site-block">
    <toy-header></toy-header>

    <toy-list :toys="toys" v-if="toys"></toy-list>

    <div class="add-btn-container">
      <router-link class="add-btn" to="/toy/edit">+</router-link>
    </div>
  </div>
</template>

<script>
import toyList from "@/cmps/toy-cmps/toy-list";
import toyFilter from "@/cmps/toy-cmps/toy-filter";
import toyHeader from "@/cmps/toy-cmps/toy-header";

export default {
  methods: {
    filterToys(filterBy) {
      this.$store.commit({ type: "filterToys", filterBy });
      this.$store.dispatch({ type: "loadToys" });
    },
    sortToys(sortBy) {
      this.$store.commit({ type: "sortToys", sortBy });
      this.$store.dispatch({ type: "loadToys" });
    }
  },
  computed: {
    toys() {
      return this.$store.getters.toys;
    }
  },
  created() {
    const user = this.$store.getters.loggedinUser;
    if (!user) this.$router.push("/login");
    const filterBy = {
      txt: "",
      type: "All",
      inStock: false
    };
    this.$store.commit({ type: "filterToys", filterBy });
    this.$store.dispatch({ type: "loadToys" });
  },
  components: {
    toyList,
    toyFilter,
    toyHeader
  }
};
</script>