<template>
    <section class="user-profile" v-if="user">
        <h1>Profile</h1>
        <form @submit="updateUser()">
            Username: <input type="text" v-model="userCopy.username" />
            <div class="fav-color-containet">
            Favorite Color: <div :style="favColorStyle" class="fav-color"></div> 
            </div>
            Set:
            <color-picker v-model="userCopy.prefs.color"></color-picker>
            <button>Save</button>
        </form>
    </section>
</template>

<script>
import colorPicker from "@/cmps/color-picker";

export default {
 data() {
        return {
            userCopy: JSON.parse(JSON.stringify(this.$store.getters.loggedinUser)) 
        }
    },
    methods: {
        updateUser() {
            this.$store.commit({ type: 'updateUser', user: JSON.parse(JSON.stringify(this.userCopy))})
            this.$router.push('/')
        }
    },
    computed: {
        user() {
            return this.$store.getters.loggedinUser
        },
        favColorStyle() {
            return {backgroundColor: '#' + this.userCopy.prefs.color}
        }
    },
    created() {
        if (!this.$store.getters.loggedinUser) this.$router.push('/login')
    },
    components: {
    colorPicker
    }
}
</script>