<template>
  <section class="toy-details" v-if="toy">
    <img :src="imgUrl" alt="product" />
    <div class="name">{{toy.name | capitalize}}</div>
    <div :class="stockClass">{{inStock}}</div>
    <div class="price">{{toy.price | price}}</div>
    <div class="type">Type: {{toy.type}}</div>
    <div class="created">Added at: {{toy.createdAt | date}}</div>
    <div class="menu">
      <button @click="remove">Delete product</button>
      <router-link :to="'/toy/edit/' + toy._id">Edit</router-link>
      <router-link to="/">Back</router-link>
    </div>
  </section>
</template>

<script>
import { toyService } from "../../services/toy.service.js";
export default {
  data() {
    return {
      toy: null
    };
  },
  methods: {
    remove() {
      this.$store
        .dispatch({ type: "removeToy", toyId: this.toy._id })
        .then(() => {
          this.$router.push("/");
        });
    }
  },
  computed: {
    inStock() {
      return this.toy.inStock ? "In Stock" : "Out of stock";
    },
    imgUrl() {
      const defaultUrl =
        "http://res.cloudinary.com/ramus/image/upload/v1584397996/iwtdenwldxi67errhflg.png";
      return this.toy.imgUrl ? this.toy.imgUrl : defaultUrl;
    },
    stockClass() {
      return this.toy.inStock ? 'stock' : 'out-of-stock'
    }
  },
  created() {
    const toyId = this.$route.params.toyId;
    toyService.getById(toyId).then(toy => (this.toy = toy));
  }
};
</script>

<style scoped>
.toy-details img {
  height: 350px;
  width: auto;
}
.menu > * {
  margin: 10px;
}
</style>