import { httpService } from './http.service.js'
const baseUrl = 'toy'


export const toyService = {
    query,
    getById,
    getEmptyToy,
    remove,
    save
}

function query(criteria) {

    return httpService.get(baseUrl, criteria)
}

function getById(id) {
    return httpService.get(`${baseUrl}/${id}`)
}

function remove(id) {
    return httpService.delete(`${baseUrl}/${id}`)
}

function save(toy) {
    if (toy._id) {
        return httpService.put(`${baseUrl}/${toy._id}`, toy)
    } else {
        return httpService.post(`${baseUrl}`, toy)
    }
}


function getEmptyToy() {
    return {
        name: '',
        price: 0,
        type: 'Funny',
        createdAt: Date.now(),
        inStock: true
    }
}
