import { utilService } from '../services/util.service.js'

export const chatStore = {
  state: {
    msgs: [],
    isChat: false
  },
  getters: {
    msgs(state) {
      return state.msgs
    },
    isChat(state) {
      return state.isChat
    }
  },
  mutations: {
    sendMsg(state, { txt }) {
      const newMsg = {
        _id: utilService.makeId(),
        txt: 'You: ' + txt
      }
      state.msgs.push(newMsg)
    },
    reply(state) {
      const newMsg = {
        _id: utilService.makeId(),
        txt: 'Support: Im not going to reply..'
      }
      state.msgs.push(newMsg)
    },
    toggleChat(state) {
      state.isChat = !state.isChat
    },
    closeChat(state) {
      state.isChat = false
    }
  },
  actions: {
    reply(context) {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          context.commit({ type: 'reply' })
          resolve()
        }, 1200);
      })

    }
  }
}