<template>
  <header class="toy-header site-block">

    <div class="sort-box">
      <label for="inStock">Only in stock</label>
      <input id="inStock" @change="filterToys(filterBy)" type="checkbox" v-model="filterBy.inStock" />

      <select placeholder="Sort By" class="sort-select" @change="sortToys(sortBy)" v-model="sortBy">
        <option value="null" disabled selected hidden>Sort</option>
        <option value="name">Name</option>
        <option value="price">Price</option>
      </select>
    </div>
  </header>
</template>

<script>

export default {
  data() {
    return {
      filterBy: null,
      sortBy: null
    };
  },
  methods: {
    getFilter() {
      this.filterBy = JSON.parse(JSON.stringify(this.$store.getters.filterBy));
    },
    filterToys(filterBy) {
      this.$store.commit({ type: "filterToys", filterBy });
      this.$store.dispatch({type: 'loadToys'})
    },
    sortToys(sortBy) {
      this.$store.commit({ type: "sortToys", sortBy });
      this.$store.dispatch({type: 'loadToys'})
    }
  },
  watch: {
    "this.$store.getters.filterBy.txt"(oldVal, newVal) {
      this.filterBy.txt = newVal;
    }
  },
  created() {
    this.getFilter();
  },

};
</script>

