<template>
    <nav class="nav-filter nav-flex">
        <div @click="emitInput('Funny')" class="nav-item">Funny</div>
        <div @click="emitInput('Educational')" class="nav-item">Educational</div>
        <div @click="emitInput('Adult')" class="nav-item">Adult</div>
        <div @click="emitInput('All')" class="nav-item">All</div>
    </nav>
</template>

<script>
export default {
    props: {
        value: String
    },
    methods: {
        emitInput(val) {
            this.$emit('input', val)
            if (this.$route.path !== '/') this.$router.push('/')
        }
    }
}
</script>