<template>
  <section class="signup-page">
        <form @submit.prevent="singup(credentials)">
            <input name="email" type="email" v-model="credentials.email"
            class="login-box" placeholder="Email"/>
            <input name="username" type="text" v-model="credentials.username"
            class="login-box" placeholder="Username"/>
            <input name="pass" type="password" v-model="credentials.password"
            class="login-box" placeholder="Password"/>
            <input name="confirmPass" type="password" v-model="confirmPass"
            class="login-box" placeholder="Confirm your password"/>
            <button class="signup-btn classic-btn">Sign Up</button>
        </form>

        Already have a user? <router-link to="/login">Login</router-link>
    </section>
</template>

<script>

export default {
 data() {
        return {
            credentials: {
                email: '',
                username : '',
                password: ''
            },
            confirmPass: ''
        }
    },
    methods: {
        async singup(credentials) {
            if(this.credentials.password === this.confirmPass) {
              await this.$store.dispatch({type: 'signup', credentials})
                this.$router.push('/')

               } else console.log('Wrong confirm password');
        }
    }
}
</script>

