<template>
  <section v-if="toy" class="toy-edit">
    <h2>{{(toy._id)? 'Edit' : 'Add'}} Toy</h2>

    <form class="edit-form" @submit.prevent="saveToy(toy)">
      <label>Toy name:</label>
      <input type="text" v-model="toy.name" placeholder="Toy name" />
      <label>Toy price:</label>
      <input type="number" v-model="toy.price" placeholder="Toy price" />
      <input type="file" @change="uploadImg" placeholder="Upload product image"/>
      <button v-if="!isUploading" class="save-btn">Save</button>
    </form>
  </section>
</template>

<script>
import {toyService} from "@/services/toy.service";
import {cloudinaryService} from "@/services/cloudinary.service";
export default {
  data() {
    return {
      toy: null,
      isUploading: false
    };
  },
  methods: {
    saveToy(toy) {
      toy.price = +toy.price
      toy.name = toy.name.toLowerCase()
      this.$store.dispatch({ type: "saveToy", toy }).then(toy => {
        this.$router.push("/");
      }).catch(err => console.log(err))
    },
    async uploadImg(ev) {
      this.isUploading = true
      try {
        const imgUrl = await cloudinaryService.uploadImg(ev)
        this.toy.imgUrl = imgUrl
      } catch {
        console.log('Uploading failed')
      }
      this.isUploading = false
    }
  },
  created() {
    let toyId = this.$route.params.toyId;
    if (toyId) {
      toyService.getById(toyId).then(toy => {
        this.toy = JSON.parse(JSON.stringify(toy));
      });
    } else this.toy = toyService.getEmptyToy();
  }
};
</script>

